import * as React from "react";
export function Performance(props) {
    const [samples, setSamples] = React.useState(SamplesOverTime.empty);
    React.useEffect(() => {
        const { dispose } = props.samplesSignal.subscribe(samples => setSamples(x => x.update(samples)));
        return dispose;
    }, [props.samplesSignal]);
    const samplecount = samples.length();
    let max = Number.MIN_VALUE;
    let sum = 0;
    samples.forEach((sample, index) => {
        const time = sample.endMs - sample.startMs;
        sum += time;
        if (time > max) {
            max = time;
        }
    });
    return (React.createElement("div", null,
        React.createElement("div", null,
            "Max: ",
            max.toFixed(3),
            " ms"),
        React.createElement("div", null,
            "Avg: ",
            (sum / samplecount).toFixed(3),
            " ms")));
}
class SamplesOverTime {
    constructor(sample0, sample1, sample2, sample3) {
        this.sample0 = sample0;
        this.sample1 = sample1;
        this.sample2 = sample2;
        this.sample3 = sample3;
    }
    update(sample) {
        return new SamplesOverTime(sample, this.sample0, this.sample1, this.sample2);
    }
    length() {
        return this.sample0.length + this.sample1.length + this.sample2.length + this.sample3.length;
    }
    forEach(f) {
        let samples = this.sample0;
        let length = 0;
        for (let i = 0; i < samples.length; i++) {
            f(samples[i], i);
        }
        length += samples.length;
        samples = this.sample1;
        for (let i = 0; i < samples.length; i++) {
            f(samples[i], length + i);
        }
        length += samples.length;
        samples = this.sample2;
        for (let i = 0; i < samples.length; i++) {
            f(samples[i], length + i);
        }
        length += samples.length;
        samples = this.sample3;
        for (let i = 0; i < samples.length; i++) {
            f(samples[i], length + i);
        }
    }
}
SamplesOverTime.empty = new SamplesOverTime([], [], [], []);
class CircularArray {
    constructor(size, defaultValue) {
        this.size = size;
        this.defaultValue = defaultValue;
        this.nextIdx = 0;
        const array = new Array(size);
        for (let i = 0; i < size; i++) {
            array[i] = defaultValue;
        }
        this.array = array;
    }
    push(value) {
        const nextIdx = this.nextIdx;
        this.array[nextIdx] = value;
        this.nextIdx = (nextIdx + 1) % this.size;
    }
}
